import * as types from './mutation-types'

// 
export const selectPlaylist = function ({
  commit,
  state
}, payload) {
  // 
  commit(types.SET_SEQUENCE_LIST, payload.list)
  // 
  commit(types.SET_PLAYLIST, payload.list)
  // 
  commit(types.SET_CURRENT_INDEX, payload.index)
  // 
  commit(types.SET_FULL_SCREEN, true)
  // 
  commit(types.SET_PLAYING_STATE, true)
}



